html {
    font-size: 16px; 
}

body {
    position: relative;
    background: #5C6BC0;
    min-height: 200vh;
    /* color: white; */
    font-family: 'Futura', 'Helvetica', sans-serif;
    font-weight: normal;
}

nav {
    z-index: 1;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    height: 50px;
    background-color: rgba(#4DD0E1, 0.85);
}

.hero-container {
    position: relative;
    width: 100%;
    height: 100vh;
    overflow: hidden;
}

.hero-image {
    width: 100vw;
    height: 95vh; 
    background-size: cover;
    background-position: center;
}

.hero-children {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    
    display: -ms-flexbox;
    
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
}

h1 {
    font-weight: normal;
    font-size: 6vw;
}




